export default {
	data() {
		return {
			couponValidList:[],
			mycouponvalidList:[],
			headerTop:0,
			//控制滑动效果
			typeClass:'valid',
			subState:'',
			theIndex:null,
			oldIndex:null,
			isStop:false
		}
	},
	onPageScroll(e){
		
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
	    setTimeout(function () {
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	onLoad(option) {
		let getphone = parseInt(option.phone);
		console.log(getphone,'传过来的手机号')
		//兼容H5下排序栏位置










		this.couponlist();
		this.mycouponlist();
	},
	methods: {
		//领取优惠券
		dogetcoupon(id){
			uni.showLoading({title: '领取中...'});
			this.getcoupon(id)
		},
		//获取优惠券列表
		async couponlist(){
			let getopenid=uni.getStorageSync('openid');
			let info= await this.$apis.couponlist({openId:getopenid});
			this.couponValidList=info.data.couponList;
		},
		//获取我的优惠券
		async mycouponlist(){
			let getopenid=uni.getStorageSync('openid');
			let info= await this.$apis.mycouponlist({openId:getopenid});
			this.mycouponvalidList=info.data.couponList;
		},
		async getcoupon(id){
			let getopenid=uni.getStorageSync('openid');
			let info= await this.$apis.getcoupon({openId:getopenid,ticketId:id});
			if(info.data.code==200){
				uni.hideLoading();
				uni.showToast({
					title:'领取成功',icon:'none',duration:2000
				})
				this.couponlist();
			}else{
				uni.showToast({
					title:'领取失败',icon:'fail',duration:1500
				})
			}
		},
		switchType(type){
			this.couponlist();
			this.mycouponlist();
			if(this.typeClass==type){
				return ;
			}
			uni.pageScrollTo({
				scrollTop:0,
				duration:0
			})
			this.typeClass = type;
			this.subState = this.typeClass==''?'':'show'+type;
			setTimeout(()=>{
				this.oldIndex = null;
				this.theIndex = null;
				this.subState = this.typeClass=='valid'?'':this.subState;
			},200)
		},
		
		
		
	}
}
